.contact_page_full-screen{
  background: #fff;
  height: 80px;
  .menu__list > li > a{
    font-family: @helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 135%;
    /* or 24px */

    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    /* Ҹ���� ������ */

    color: #2B2A28;

  }
}
.firm_name{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */
  letter-spacing: 0.05em;

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.firm_adress{
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}
.firm_adress p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;

}
.firm_tel p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
}
.time_work_title{
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;

}
.time_work_work{
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-left: 40px;
}
.contacts_block_wrapper{
  width: 1080px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 100px 0;

}
.firm_wrapper{
  width: 490px;
  display: flex;
  flex-direction: column;
}
.time_work_sub_inner{
  display: flex;

}
.google_maps_wrapper iframe{
  width: 100%;
  height: 483px;
}
.contact_form_wrapper{
  background: #E34925;
  padding: 70px 0;
}
.contact_form_block{
  width: 1080px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.contact_form_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;

}
.contact_fitback{
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.contact_fitback_input_form{
  width: 49%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.russia_icon{
  position: absolute;
  left: 12px;
  bottom: 53px;
}
.text_area_wrapper{
  width: 49%;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}
.contact_fitback_input_form input{
  height: 60px;
  padding-left: 35px;
  background: #E34925;
  border: 1px solid #F5F5F5;
  outline: none;
  margin-top: 30px;
}
.tel_padding{
  padding-left: 50px;
}
input[type="text"]{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 145%;
  color: rgba(255,255,255, 0.8);
}

.contact_fitback_input_form input:nth-child(3){
  padding-left: 50px;
}
.contact_fitback_btn{
  
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */
  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* �������� ��������� */

  color: #E34925;

  height: 60px;
  text-align: center;
  margin-top: 23px;
  border: 1px solid #F5F5F5;
}
.privacy_text{
  width: 97%;

  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 12px;
  /* or 100% */

  text-align: center;

  /* ������-����� */

  color: rgba(255,255,255, 0.8);
  margin-top: 15px;
}
.text_area_wrapper textarea{
  resize: none;
  background: #E34925;
  outline: none;
  border: 1px solid #F5F5F5;
}
textarea[type="text"]{
  padding-top: 15px;
  padding-left: 35px;
}



@media screen and (max-width: 1030px){
  .contacts_block_wrapper{
    width: 924px;
  }
  .contact_form_block{
    width: 484px;
  }
  .contact_fitback{
    flex-direction: column;
  }
  .contact_fitback_input_form{
    width: 100%;
  }
  .text_area_wrapper{
    width: 100%;
  }
  .russia_icon{
    bottom: 15px;
  }
  .contact_fitback_input_form input{
    margin-top: 20px;
  }
}
@media screen and (max-width: 993px){
  .contacts_block_wrapper{
    flex-direction: column;
    width: 420px;
    margin-top: 70px;
    align-items: baseline;
  }
  .time_work_sub_inner{
    flex-direction: column;
  }
  .time_work_work{
    margin-left: 0;
  }
  .time_work_inner{
    margin-top: 30px;
  }
}
@media screen and (max-width: 576px){
  .contacts_block_wrapper{
    margin-top: 50px;
    padding-top: 0;
    padding-bottom: 50px;
    width: 95%;
  }
  .firm_name{
    font-size: 22px;

  }
  .firm_adress p{
    font-size: 16px;
  }
  .contact_form_block{
    width: 95%;
  }
  .contact_form_title{
    font-size: 22px;
    margin: 0 auto;
    color: #2B2A28;
  }
  .contact_form_wrapper{
    background: #F5F5F5;
  }
  .contact_fitback_input_form input{
    background: #F5F5F5;
    border: none;
    border-bottom: 1px solid #2B2A28;
  }
  .text_area_wrapper textarea{
    display: none;
  }
  .contact_fitback_btn{
    background: #E34925;
    color: #FFFFFF;
  }
  .privacy_text{
    font-size: 10px;
    color: #262626;

    opacity: 0.4;
text-align: start;
  }
  .privacy_text a{
    font-size: 10px;
    color: #262626;

    /*opacity: 0.4;*/
    font-family: @helvetica;
    font-style: normal;
    font-weight: 300;
    line-height: 12px;
    /* or 120% */
  }
  .firm_wrapper{
    width: 100%;
  }
}