* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 1024px) {
  body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
  }
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
ol {
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
  font-family: inherit;
}
button {
  outline: none;
  cursor: pointer;
  transition: all ease-in 0.2s;
}
.container {
  max-width: 1160px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}
@media screen and (max-width: 1200px) {
  .container {
    max-width: 1170px;
  }
}
@media screen and (max-width: 993px) {
  .container {
    padding: 0 10px;
  }
}
@media screen and (max-width: 576px) {
  .container {
    padding: 0 10px;
  }
}
.overlay {
  width: 100%;
  height: 100%;
  z-index: 8;
  background-color: rgba(25, 42, 24, 0.7);
  position: fixed;
  top: 0;
  left: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}
