  flex-direction: column;
  align-items: center;
}
.project_content_block_row_rev .project_content_content_wrapper {
  flex-direction: row-reverse;
}
.project_content_title {
  background: #F5F5F5;
  text-align: center;
  padding: 57px 0;
  font-family: "Mont extra bold", "Mont", "Mont Regular", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 26px;
  line-height: 135%;
  /* or 35px */
  letter-spacing: 0.05em;
  text-transform: uppercase;
  /* Ҹ���� ������ */
  color: #2B2A28;
  width: 100%;
}
.project_content_title_768 {
  display: none;
}
.project_content_content_wrapper {
  display: flex;
  width: 100%;
  align-items: center;
}
.project_sub_title {
  margin-top: 20px;
  font-family: "Helvetica Neue Cyr Bold", "Helvetica Neue Cyr Medium", "Helvetica Neue Cyr Light", "Helvetica Neue Cyr", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */
  display: flex;
  /* align-items: center;
   text-align: center;*/
  letter-spacing: 0.02em;
  /* ������-����� */
  color: #F5F5F5;
  /* 1 */
  flex-direction: column;
  text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
}
.project_text_title {
  font-family: "Helvetica Neue Cyr Bold", "Helvetica Neue Cyr Medium", "Helvetica Neue Cyr Light", "Helvetica Neue Cyr", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 145%;
  /* identical to box height, or 29px */
  /* Ҹ���� ������ */
  color: #2B2A28;
}
.project_text_text {
  font-family: "Helvetica Neue Cyr Bold", "Helvetica Neue Cyr Medium", "Helvetica Neue Cyr Light", "Helvetica Neue Cyr", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */
  /* Ҹ���� ������ */
  color: #2B2A28;
  margin-top: 10px;
}
@media screen and (max-width: 1030px) {
  .our_project_full-screen {
    background: url("../img/our_project_bg_1024.png");
    background-size: cover;
    min-height: 920px;
  }
  .project_title {
    font-size: 42px;
  }
  .project_sub_title {
    font-size: 26px;
    width: 63%;
    text-align: center;
  }
  .project_content_title {
    padding: 40px 116px;
  }
  .project_content_title_768 {
    display: none;
  }
  .project_content_title_mod_1 {
    padding: 40px 100px;
  }
  .project_content_title_mod_3 {
    padding: 40px 80px;
  }
  .project_content_title_mod_4 {
    padding: 40px 156px;
  }
  .project_content_title_mod_5 {
    padding: 40px 145px;
  }
  .project_text_wrapper {
    width: 50%;
    margin-right: 0;
  }
  .project_text {
    width: 356px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .project_img_bg {
    width: 50%;
  }
  .project_img_bg :nth-child(1) {
    display: none;
  }
  .project_img_bg :nth-child(2) {
    display: block;
  }
  .project_img_bg :nth-child(3) {
    display: none;
  }
  .project_img_bg :nth-child(4) {
    display: none;
  }
  .project_text_text {
    font-size: 16px;
  }
  .project_content_block_row_rev .project_content_content_wrapper {
    flex-direction: row;
  }
}
@media screen and (max-width: 993px) {
  .our_project_full-screen {
    background: url("../img/our_project_bg_768.png");
    background-size: cover;
    min-height: 920px;
  }
  .project_title-wrapper {
    padding-top: 350px;
  }
  br {
    display: none;
  }
  .project_content_title {
    font-size: 22px;
    width: 541px;
    position: absolute;
    border: 1px solid #2B2A28;
    background: #F5F5F5;
    top: 33%;
    /* left: 50%;
     transform: translate(-50%, -50%);*/
    padding: 40px 21px;
    display: none;
  }
  .project_content_title_768 {
    width: 541px;
    position: absolute;
    border: 1px solid #2B2A28;
    background: #F5F5F5;
    top: 3%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 40px 21px;
    font-family: "Mont extra bold", "Mont", "Mont Regular", sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 22px;
    line-height: 135%;
    /* or 30px */
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    /* Ҹ���� ������ */
    color: #2B2A28;
  }
  .project_content_title_mod_1 {
    padding: 40px 0;
  }
  .project_content_title_mod_3 {
    padding: 40px 10px;
  }
  .project_content_content_wrapper {
    flex-direction: column-reverse;
  }
  .project_content_block_row_rev .project_content_content_wrapper {
    flex-direction: column-reverse;
  }
  .project_img_bg {
    width: 100%;
  }
  .project_img_bg :nth-child(1) {
    display: none;
  }
  .project_img_bg :nth-child(2) {
    display: none;
  }
  .project_img_bg :nth-child(3) {
    display: block;
  }
  .project_img_bg :nth-child(4) {
    display: none;
  }
  .project_text_wrapper {
    width: 100%;
    padding-top: 170px;
    padding-bottom: 70px;
    position: relative;
  }
  .project_text {
    width: 541px;
  }
}
@media screen and (max-width: 576px) {
  .project_title {
    font-size: 26px;
  }
  .our_project_full-screen {
    background: url("../img/our_project_bg_375.png");
    background-size: cover;
    min-height: 724px;
  }
  .project_content_title_768 {
    position: inherit;
    background: #ffffff;
    border: none;
    width: 100%;
    padding: 0;
  }
  .project_text_wrapper {
    padding-top: 0px;
  }
  .project_img_bg {
    width: 100%;
  }
  .project_img_bg :nth-child(1) {
    display: none;
  }
  .project_img_bg :nth-child(2) {
    display: none;
  }
  .project_img_bg :nth-child(3) {
    display: none;
  }
  .project_img_bg :nth-child(4) {
    display: block;
  }
  .project_text {
    width: 95%;
  }
  .project_content_title_768 {
    margin-top: 80px;
    width: 94%;
  }
  .project_title-wrapper {
    padding-top: 250px;
  }
  .project_sub_title {
    width: 97%;
  }
  .project_content_title_mod_3 {
    width: 100%;
  }
}
