.service_full-screen {
  min-height: 1080px;
  background: url("../img/service_bg.png") no-repeat;
  background-size: cover;
  @media screen and (max-width: 1030px) {
    background: url("../img/service_bg_1024.png") no-repeat;
    background-size: cover;
    min-height: 1024px;
  }
  @media screen and (max-width: 993px) {
    background: url("../img/service_bg-768.png") no-repeat;
    background-size: cover;
    min-height: 1260px;
    position: relative;
  }
}

.service_title-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.burger_menu_wrapper {
  display: none;
  @media screen and (max-width: 993px) {
    display: flex;
    position: absolute;

    height: 20px;
    top: 50px;
    right: 30px;
  }
}

.burger_sub_wrapper {
  @media screen and (max-width: 993px) {
    position: relative;
    display: flex;
    align-items: center;
    width: 30px;
  }
}

.burger_sub_wrapper span {
  @media screen and (max-width: 993px) {
    width: 24px;
    height: 2px;
    background: #FFFFFF;
    border-radius: 1.00015px;
    transform: matrix(-1, 0, 0, 1, 0, 0);
    display: block;
    margin-left: auto;
  }
}

.burger_sub_wrapper:before,
.burger_sub_wrapper:after {
  @media screen and (max-width: 993px) {
    content: '';
    display: block;
    width: 30px;
    height: 2px;
    position: absolute;
    background: #FFFFFF;
  }
}

.burger_sub_wrapper:before {
  @media screen and (max-width: 993px) {
    top: 0;
  }
}

.burger_sub_wrapper:after {
  @media screen and (max-width: 993px) {
    bottom: 0;
  }
}

