  }
}

.tabs-item {
  position: relative;
  height: 360px;
}

.tabs-item:after {
  position: absolute;
  content: '';
  display: block;
  top: 0;
  right: -99px;
  border-style: solid;
  border-width: 180px 0 180px 100px;
  border-color: transparent transparent transparent #E34925;
  opacity: 0;
}

.tabs-item:hover {
  background: #E34925;
  .tabs_sub-item:before {
    border-left: 5px solid #F5F5F5;
    position: absolute;
    content: '';
    left: 0;
    width: 5px;
    height: 192px;
    @media screen and (max-width: 1030px) {
      display: none;
    }
  }
  .tabs-title,
  .tabs-text {
    color: #F5F5F5;
  }
  &:after {
    opacity: 1;
  }
}

