.doc_dev_full-screen {
  background: url("../img/dev_dov_bg.png");
  background-size: cover;
  min-height: 1080px;
}

.doc_descr_wrapper {
  display: flex;
  width: 100%;
  align-items: center;
}

.doc_descr_text_wrapper {
  width: 50%;
}

.doc_descr_img {
  width: 50%;
  :nth-child(2) {
    display: none;
  }
  :nth-child(3) {
    display: none;
  }
}

.doc_descr_img img {
  height: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
}

.doc_descr_text p {
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* Ҹ���� ������ */
  color: #2B2A28;
  margin-top: 30px;
  margin-left: auto;
}

.doc_descr_text span {
  font-weight: 700;
}

.doc_descr_text {
  width: 500px;
  margin-left: auto;
  margin-right: 73px;
}

.stage_dev_wrapper {
  background: #E34925;
  display: flex;
  align-items: center;
}

.stage_dev_sub_wrapper {
  display: flex;
  flex-direction: column;
  width: 1170px;
  margin: 0 auto;
  padding-top: 76px;
  padding-bottom: 75px;
}

.stage_dev_main_title {
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
  margin-bottom: 43px;
}

.stage_points_inner {
  display: flex;
  width: 100%;
}

.stage_points_list {
  list-style: disc;
  position: relative;
  padding-left: 40px;

  li {
    width: 345px;
    margin-top: 20px;
    font-family: @helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 145%;
    /* or 26px */

    /* ������-����� */

    color: #F5F5F5;
  }
}

.stage_points_list_2 {

  position: relative;
  padding-left: 40px;

  li {
    margin-top: 20px;
    font-family: @helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 145%;
    /* or 26px */

    /* ������-����� */

    color: #F5F5F5;
  }
}

.decor_nubmer {
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
}

.stage_points_sub_inner {
  display: flex;
  align-items: baseline;
  width: 45%;
}

.stage_points_title {
  font-family: @helvetica;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
  width: 422px !important;
  list-style: none;
}

.stage_project_wrapper {
  padding-bottom: 75px;
}

.stage_project_sub_wrapper {
  display: flex;
  flex-direction: column;
  width: 1150px;
  margin: 0 auto;
}

.stages_wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 92px;
}

.stages_title {
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */
  letter-spacing: 0.05em;
  text-transform: uppercase;
  /* Ҹ���� ������ */
  color: #2B2A28;
  position: relative;

}

.stages_title:before {
  position: absolute;
  content: '';
  display: block;
  top: -30px;
  left: 0;
  background: #2B2A28;
  height: 5px;
  width: 100px;
}

.stages_descr_wrapper {
  display: flex;
  width: 100%;
  align-items: baseline;
  justify-content: space-between;
}

.stages_descr {
  width: 45%;
}

.stages_descr p {
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-top: 40px;
}

.manager_wrapper {
  width: 100%;
  background: #F5F5F5;
  padding-top: 37px;
  padding-bottom: 100px;
}

.manager_sub_wrapper {
  width: 1150px;
  margin: 0 auto;
}

.manager_title {

  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-bottom: 17px;

}

.manager_table {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}

.manager_table_item {
  border: 1px solid #2B2A28;
  width: 362px;
  text-align: start;
  padding: 30px 55px;

  font-family: @helvetica;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  letter-spacing: 0.05em;

  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-top: 30px;
  span {
    font-weight: 500;
  }
}

.primely_sub_wrapper {
  width: 1150px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding-top: 70px;
  padding-bottom: 100px;
}

.primely_title {
  font-family: @helvetica;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */
  /* Ҹ���� ������ */
  color: #2B2A28;
  position: relative;
  padding-left: 30px;
  width: 100%;
}

.primely_title:before {
  display: block;
  position: absolute;
  content: '';
  left: 1px;
  top: -34px;
  background: #2B2A28;
  width: 5px;
  height: 100px;
}

.primely_blocks_inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 70px;
}

.primely_blocks_sub_inner {
  background: #E34925;
  width: 31.7%;
  height: 400px;
  padding-top: 82px;
}

.primely_descr_inner {
  width: 274px;
  margin: 0 auto;
}

.primely_descr_title {
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 135%;
  /* identical to box height, or 27px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
  position: relative;
}

.primely_descr_title:after {
  content: '';
  display: block;
  position: absolute;
  top: -20px;
  left: 0;
  background: #F5F5F5;
  width: 50px;
  height: 5px;
}

.primely_descr_text {
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* ������-����� */

  color: #F5F5F5;
  margin-top: 20px;
}

.primely_descr_bot_wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 70px;
}

.primely_descr_bot_wrapper p {
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* Ҹ���� ������ */

  color: #2B2A28;
  width: 49%;
  span {
    font-weight: 500;
  }
}

.doc_descr_wrapper_bot {
  background: #F5F5F5;
}

.descr_min_block {
  width: 100%;
  padding: 100px 0;
}

.descr_min_inner {
  width: 1150px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

}

.descr_min_inner p {
  width: 49%;
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.descr_min_block_2{
  display: none;
}
.doc_dev_full-screen{
  .service_title{
    margin-top: 290px;
  }

}
@media screen and (max-width: 1030px) {
  .doc_dev_full-screen {
    background: url("../img/dev_dov_bg_1024.png");
    background-size: cover;
  }

  .service_title {
    margin-top: 209px;
  }

  .doc_descr_text_wrapper {
