    margin-left: auto;
    span {
      font-size: 16px;
    }
  }

  .doc_descr_img {
    :nth-child(1) {
      display: none;
    }
    :nth-child(2) {
      display: block;

    }
    :nth-child(3) {
      display: none;
    }
  }

  .stage_dev_sub_wrapper {

  }

