    font-size: 25px;
    width: 786px;
    margin: 0 auto;
  }

  .stage_points_inner {
    width: 90%;
    margin: 0 auto;
    margin-top: 70px;
    justify-content: flex-end;
  }

  .stage_points_sub_inner {
    flex-direction: column;
    align-items: baseline;

  }

  .stage_points_list {
    padding-left: 0;
  }

  .stage_points_list_2 {
    padding-left: 0;
  }

  .stage_project_sub_wrapper {
    width: 73%;
    margin: 0 auto;

  }

  .stages_wrapper {
    margin-top: 105px;
  }

  .stages_title {
    font-size: 26px;
  }

  .stages_descr {
    width: 47%;
  }

  .manager_sub_wrapper {
    width: 95%;
  }

  .manager_table_item {
    width: 31%;
    padding: 30px 25px;
    font-size: 16px;
    span {
      font-size: 16px;
    }
  }

  .primely_title {
    width: 73%;
