  }

  .primely_descr_bot_wrapper {
    width: 90%;
    margin: 0 auto;
    margin-top: 70px;
    p {
      width: 45%;
    }
  }

  .descr_min_inner {
    width: 86%;
    margin: 0 auto;
  }

  .doc_descr_wrapper_bot {
    flex-direction: column-reverse;
    .doc_descr_text_wrapper {
      width: 100%;
      padding-top: 45px;
      padding-bottom: 70px;
      .doc_descr_text {
        width: 100%;
        p {
          width: 86%;
          margin: 0 auto;
          margin-top: 25px;
        }
      }
    }
    .doc_descr_img {
      width: 100%;
    }
  }

  .descr_min_block {
    padding: 70px 0;
    width: 100%;
    margin: 0 auto;
  }

  .descr_min_inner p {
    width: 44%;
  }
}

