.fire_system_page{
  .service_full-screen{
    //min-height: 1080px;
    background: url(../img/fire_bg.png) no-repeat;
    background-size: cover;
  }
  .service_title{
    width: 50%;
  }
  .service_text{
    width: 30%;
  }
  .service_bg-wrapper{
    width: 100%;
  }
  .control_slide_2{
   background: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 124px;
  }
  .service_link{
    width: 362px;
    text-align: center;
  }
  .info_icon{
    border: 1px solid #E34925;
    border-radius: 50%;
  }
  .potom_ydalitb_1{
    display: none !important;
  }
  .system_warning_slide{
    display: flex;
    width: 100%;
    height: 750px;
  }
  .system_warning_wrapper{
    width: 90%;
    background: #F5F5F5;
    padding-top: 176px;
    padding-left: 267px;
  }
  .system_warning_sub_wrapper{
    width: 50%;
    margin-left: auto;
    margin-right: 90px;
  }
  .system_warning_text p{
    font-family: @helvetica;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 145%;
    /* or 29px */


    /* Ҹ���� ������ */

    color: #2B2A28;
    margin-top: 30px;
  }
  .system_warning_img{
    width: 50%;
    margin-bottom: -4px;
  }
  .system_warning_img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-height: 1080px;
  }
  .system_warning_title{
    font-family: @mont;
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 135%;
    /* or 43px */

    letter-spacing: 0.05em;
    text-transform: uppercase;

    /* Ҹ���� ������ */

    color: #2B2A28;
    position: relative;
  }
  .system_warning_title:before{
    position: absolute;
    content: '';
    width: 100px;
    height: 5px;
    background: #2B2A28;
    display: block;
    top: -30px;
    left: 0;
  }
  .form_slide{
    display: flex;
    width: 100%;
    background: url("../img/form_fire_bg.png") no-repeat;
    background-size: cover;
    min-height: 750px;
    align-items: center;
  }
  .form_text_wrapper{
    width: 50%;
  }
  .form_text_sub_wrapper{
   /* width: 635px;*/
    width: 90%;

    margin-left: auto;
  }
  .form_title{
    font-family: @mont;
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 135%;
    /* or 43px */

    letter-spacing: 0.05em;
    text-transform: uppercase;

    /* ������-����� */

    color: #F5F5F5;
  }
  .form_text{
    font-family: @helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 145%;
    /* or 29px */


    /* ������-����� */

    color: #F5F5F5;
    margin-top: 15px;
    width: 90%;
  }
  .form_field_wrapper{
    width: 50%;
  }
  .form_field_sub_wrapper{
    /*width: 572px*/;
    width: 95%;
    background: #F5F5F5;
  }
  .form_list{
    display: flex;
    flex-direction: column;
    padding: 52px 48px 69px 62px;
  }
  .form_list input{
    border: none;
  }
  .form_list{
    input[type="text"]{
      font-family: @helvetica;
      font-style: normal;
      font-weight: 300;
      font-size: 20px;
      line-height: 145%;
      /* identical to box height, or 29px */
      /* Ҹ���� ������ */
      color: rgba(43, 42, 40, 0.5);
      padding: 6px 0;
      background: #F5F5F5;
      border-bottom: 1px solid rgba(43, 42, 40, 0.5);
      margin-bottom: 50px;
      outline: none;
    }
    input[type="text"]:nth-of-type(2){
      padding-left: 40px;
      /*position: relative;*/
      background: url("../img/russia_circle.svg") no-repeat 100%;
      background-position-x: left;
    }
    /*input[type="text"]:nth-of-type(2):before{
      position: absolute;
      left: 0;
      top: 0;
      content: url("../img/russia_circle.svg");
      width: 30px;
      height: 30px;
      display: block;
    }*/
  }
  .form_list_submit{
    font-family: @helvetica;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 145%;
    /* or 26px */

    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    background: #E34925;
    border: none;
    padding: 10px 0;
    margin-bottom: 19px;
  }
  .form_list p{
    font-family: @helvetica;
    font-style: normal;
    font-weight: 300;
    font-size: 10px;
    line-height: 12px;
    /* or 120% */
    color:rgba(38, 38, 38, 0.5);
    a {
      color:rgba(38, 38, 38, 0.5);
      text-decoration: underline;
    }
  }
  .control_slide_1{
    padding-bottom: 150px;
  }
  .control_slider-inner{
    .fire_system_slide{
      height: 1080px;
      @media screen and (max-width: 1030px){
        height: 768px;
      }
      
    }
  }
  @media screen and (max-width: 1445px){
    .service_title{
      width: 90%;
    }
    .service_text{
      width: 59%;
    }
  }
  @media screen and (max-width: 1030px){
    .service_full-screen{
      background: url("../img/form_fire_bg.png");
      background-size: cover;
      //min-height: 768px;
    }
    .service_title{
      font-size: 52px;
      line-height: 70px;
      width: 96%;
    }
    .service_text{
      width: 64%;
      font-size: 20px;
    }
    .control_slide_2{
      position: relative;
    }
    .service_link{
      position: absolute;
      width: 232px;
      content: '';
      bottom: 185px;
      right: 102px;

      background: #FFFFFF;
      border: 1px solid #2B2A28;
      font-family: @helvetica;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 145%;
      /* or 26px */
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;

      /* Ҹ���� ������ */
      color: #2B2A28;
      padding: 15px 20px;
    }
    .info_icon:nth-of-type(1){
      display: none;
    }
    .info_icon:nth-of-type(2){
      top: 15%;
      left: 92%;
      border: none;
    }
    .control_slide_2 .control_slider-item .control_text{
      width: 68%;
    }
    .system_warning_wrapper{
      padding-top: 87px;
      padding-left: 100px;
      padding-bottom: 50px;
    }
    .system_warning_sub_wrapper{
      width: 93%;
      margin-right: 30px;
    }
  }
  @media screen and (max-width: 993px){
    .service_full-screen{
      //min-height: 1024px;
      background: url(../img/fire_bg_768.png);
      background-size: cover;
    }
    .service_title{
      font-family: @mont;
      font-style: normal;
      font-weight: 800;
      font-size: 42px;
      line-height: 135%;
      /* or 57px */

      text-align: center;
      letter-spacing: 0.02em;
      text-transform: uppercase;

      /* ������-����� */

      color: #F5F5F5;

      /* 1 */
      width: 80%;
      text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
    }
    .service_text{
      font-family: @helvetica;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 135%;
      /* or 27px */

      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: 0.02em;

      /* ������-����� */

      color: #F5F5F5;

      /* 1 */

      text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
    }
    .system_control_wrapper{
      padding-bottom: 0;
      margin-bottom: -4px;
    }
    .md_slider_swiper{
      width: 100%;
    }
    .system_video_img{
      width: 100%;
    }
    .system_video_wrapper{
      padding-bottom: 0;
      margin-bottom: -4px;
    }
    .fire_system_slider-wrapper{
      display: none;
    }
  }
  @media screen and (max-width: 577px){
    .service_full-screen{
      //min-height: 724px;
      padding-top: 270px;
      background: url(../img/fire_bg_375.png);
      background-size: cover;
    }
    .service_title{
      font-size: 26px;
    }
    .service_text{
      font-size: 18px;
      text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);

    }
    .engineering_text{
      padding-bottom: 50px;
    }
    .system_video_text p{
      margin-top: 20px;
    }
  }
}