.teh_service_full-screen{
  background: url("../img/teh_bg.png");
  background-size: cover;
  height: 1080px;
}
.teh_main_title{
  width: 100%;
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 72px;
  line-height: 135%;
  /* or 97px */
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;

  /* 1 */

  text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
  margin-top: 286px;
  padding: 0 10%;
}
.teh_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 135%;
  /* or 27px */


  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
  position: absolute;
  top: 30px;
  left: 45px;
  width: 70%;
}
.teh_service_item{
  width: 49%;
  background: #E34925;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: space-between;
  margin-top: 30px;
}
