}*/
.teh_more_details{
  border: 1px solid #F5F5F5;
  background: #E34925;
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 145%;
  /* or 23px */

  display: flex;
