  text-align: center;
  text-transform: uppercase;

  color: #FFFFFF;
  margin-left: 45px;
  margin-top: 80px;
  padding: 15px 55px;

}
.teh_service_list{
  display: flex;
  flex-wrap: wrap;
  width: 1146px;
  margin: 0 auto;
