  padding-top: 70px;
  padding-bottom: 100px;
}
.teh_service_inner{
 /* position: relative;*/
}
.teh_popup_wrapper{
  width: 950px;
  border: 1px solid black;
  padding: 79px 0;
  position: relative;
  display: none;
  background: #fff;
}
.teh_popup_wrapper_sub_wrapper{
  display: flex;
  flex-direction: column;
  width: 754px;
  margin: 0 auto;
}
.teh_popup_text_inner{
  display: flex;
  flex-direction: column;
}
.close_btn_cross{
  width: 15px;
  height: 15px;
  top: 24px;
  right: 30px;
  position: absolute;
}
.teh_popup_text_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 135%;
  /* identical to box height, or 27px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* �������� ��������� */

  color: #E34925;

}
.teh_popup_text_content p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-top: 25px;
}
.list_disck li{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  position: relative;
  padding-left: 25px;
  margin-bottom: 5px;
}

.list_disck li:before{
  position: absolute;
  display: block;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #2B2A28;
  top: 44%;
  left: 0;
  content: '';
}
.list_disck{
  margin-top: 25px;
  margin-bottom: 50px;
}
.sub_teh_popup_text_title{
  margin-top: 50px;
}
.teh_popup_text_title_black{
  color: #2B2A28;
}


