  margin-top: 30px;
}

.teh_tabs_wrapper{
  display: flex;
  width: 100%;
  background: #D6D6D6;
}
.teh_tabs_left{
  width: 40%;
}
.teh_tabs_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */


  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Ҹ���� ������ */

  color: #2B2A28;
 text-align: end;
  margin-top: 110px;
  padding-right: 81px;
}

.teh_tabs_right_list{
  width: 60%;

}
.teh_tabs_item{
  display: flex;
  align-items: center;
 /* padding-top: 30px;
  padding-bottom: 30px;*/
  min-height: 80px;
  padding-left: 81px;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  position: relative;
}
.teh_tabs_item:first-child{
  border-top: none;
}
.teh_tabs_item:hover{
  background: #E34925;
  .teh_tabs_text{
    color: #F5F5F5;
  }
  img{
    display: none;
  }
  :before{
    position: absolute;
    display: block;
    left: -50px;
    content: '';
    top: 0;
    border-style: solid;
    border-width: 40px 50px 40px 0;
    border-color: transparent #E34925 transparent transparent;
  }
}
.teh_tabs_text{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  padding-left: 35px;
  width: 70%;
}
.user_block_wrapper{
  width: 100%;
  padding: 86px 0;
}
.user_block_descr{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.user_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.user_sub_title{
  padding-top: 10px;
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Ҹ���� ������ */

  color: #E34925;
}
.user_descr_text{
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0;
}
.user_descr_text p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  display: flex;
  align-items: center;
  text-align: center;

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.user_btn_enter{
  margin-top: 42px;
  display: flex;
  width: 362px;
  background: #E34925;
  text-align: center;
  height: 55px;
  align-items: center;
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 145%;
  /* or 23px */
  letter-spacing: 0.05em;
  text-transform: uppercase;
  justify-content: center;
  color: #FFFFFF;
}

.teh_security_status_wrapper{
  width: 100%;
  display: flex;
  align-items: center;
  background: #D6D6D6;
}
.teh_security_status_descr{
  width: 50%;
}
.teh_security_status_text_inner{
  display: flex;
  flex-direction: column;
  width: 490px;
  margin-left: auto;
  margin-right: 83px;
}
.teh_security_status_title{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */
  /* Ҹ���� ������ */
  color: #2B2A28;
}
.teh_security_status_text p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  margin-top: 25px;
}
.teh_security_status{
  width: 50%;
  :nth-child(2){
    display: none;
  }
  :nth-child(3){
    display: none;
  }
}
.teh_security_status img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;

}

@media screen and (max-width: 1025px){
  .teh_service_full-screen{
    height: 768px;
    background: url(../img/teh_bg_1024.png);
    background-size: cover;
  }
  .teh_main_title{
    font-family: @mont;
    font-style: normal;
    font-weight: 800;
    font-size: 52px;
    line-height: 135%;
    /* or 70px */

    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    /* ������-����� */

    color: #F5F5F5;

    /* 1 */
    margin-top: 209px;
    text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
  }
  .teh_service_list{
    width: 652px;
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .teh_service_item{
    width: 100%;
    height: 220px;
  }
  .teh_tabs_wrapper{
    flex-direction: column;
    padding-bottom: 70px;
  }
  .teh_tabs_left{
    width: 50%;
  }
  .teh_tabs_title{
    font-size: 26px;
    margin-top: 70px;
    margin-bottom: 30px;
    padding-right: 200px;
  }
  .teh_tabs_right_list{
    width: 80%;
    margin-left: auto;
  }
  .teh_tabs_item:first-child{
    border-top: 1px solid #fff;
  }
  .teh_tabs_item:last-child{
    border-bottom: 1px solid #fff;
  }
  .user_title{
    font-size: 26px;
  }
  .user_sub_title{
    font-size: 26px;
  }
  .user_descr_text{
    width: 80%;
  }
  .user_descr_text p{
    font-size: 18px;
  }
  .user_btn_enter{
    font-size: 16px;
    margin-top: 40px;
  }
  .user_block_wrapper{
    padding: 70px 0;
  }
  .teh_security_status_wrapper{
    flex-direction: column-reverse;

  }
  .teh_security_status_descr{
    width: 100%;
    padding: 70px 0;
  }
  .teh_security_status{
    width: 100%;
    :nth-child(1){
      display: none;
    }
    :nth-child(2){
      display: block;
    }
  }
  .teh_security_status_text_inner{
    width: 80%;
    margin: 0 auto;
  }


  .teh_popup_wrapper{
    display: none;
    width: 100%;
    position: absolute;
    top: 0;
    padding: 100px 0;
  }
  .teh_popup_wrapper_sub_wrapper{
    width: 90%;
  }
  .teh_popup_text_content p{
    font-size: 18px;
  }
}
@media screen and (max-width: 993px){
  .teh_service_full-screen{
  background: url("../img/teh_bg_768.png");
  height: 1024px;
  background-size: cover;
}
  .teh_main_title{
    margin-top: 0;
    font-size: 42px;
    padding-top: 367px;
  }
  .teh_tabs_wrapper{
    align-items: center;
  }
  .teh_tabs_left{
    width: 100%;
  }
  .teh_tabs_title{
    padding-right: 0;
    text-align: center;
    margin-top: 60px;
  }
  .teh_tabs_right_list{
    width: 100%;
    margin: 0 auto;
  }
  .teh_tabs_item:first-child{
    border: none;
  }
  .teh_tabs_item{
    border: none;
  }
  .teh_tabs_item:hover{
    background: #d6d6d6;
    .teh_tabs_text{
      color: #2b2a28;
    }
    img{
      display: none;
    }
    :before{
      position: absolute;
      display: none;
      left: 0px;
      content: '';
      border-style: solid;
      border-width: 40px 50px 40px 0;
      border-color: transparent #E34925 transparent transparent;
    }
  }
  .teh_tabs_item img{
    display: none;
  }
  .teh_tabs_text{
    font-size: 16px;
    width: 100%;
    text-align: start;
    padding-left: 30px;
    display: block;
    span{
      font-weight: 600;
    }
  }
  .teh_tabs_item{
    min-height: 30px;
    padding-left: 0;
    width: 652px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .teh_tabs_item:last-child{
    border-bottom: none;
  }
  .teh_tabs_item:before{
    display: block;
    position: absolute;
    content: '';
    background: #000000;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    top: 30%;
    left: 0;
  }
  .user_descr_text p{
    margin-top: 25px;
  }
}
@media screen and (max-width: 576px){
  .teh_service_full-screen{
    height: 1110px;
    background: url("../img/teh_bg_375.png");
    background-size: cover;
  }
  .teh_main_title{
    font-size: 26px;
    padding-top: 309px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .teh_service_list{
    width: 100%;
  }
  .teh_service_item img{
    display: none;
  }
  .teh_service_inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
  }
  .teh_title{
    position: inherit;
    width: 100%;
    font-size: 16px;
    text-align: center;
  }
  .teh_more_details{
    margin: 0;
    margin-top: 20px;
  }
  .teh_service_item{
    margin-top: 20px;
    height: 200px;
  }
  .teh_tabs_item{
    width: 100%;
  }
  .teh_tabs_right_list{
    width: 96%;
  }
  .user_title{
    font-size: 22px;
  }
  .user_sub_title{
    font-size: 22px;
  }
  .user_descr_text{
    width: 95%;
  }
  .user_descr_text p{
    font-size: 16px;
    text-align: start;
  }
  .user_btn_enter{
    width: 95%;
    margin-top: 50px;

  }
  .teh_security_status :nth-child(2){
    display: none;
  }
  .teh_security_status :nth-child(3){
    display: block;
  }
  .teh_security_status_descr{
    padding-top: 50px;
    padding-bottom: 70px;
  }
  .teh_security_status_text_inner{
    width: 95%;
  }
}
