.monitoring_full-screen{
  background: url("../img/monitoring_bg.png");
  background-size: cover;
  height: 1080px;
}
.monitoring_title-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.monitoring_title{
  width: 98%;

  margin-top: 128px;
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 72px;
  line-height: 135%;
  /* or 97px */

  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;

  /* 1 */

  text-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
}
.advantage_list{
  margin-top: 67px;

}
.advantage_list p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 145%;
  /* or 29px */
  text-align: center;
  /* ������-����� */
  color: #F5F5F5;
  width: 82%;
  margin: 0 auto;
}
.advantage_list span{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 145%;
  /* or 29px */

  text-align: center;

  /* ������-����� */

  color: #F5F5F5;

}
.advantage_list li{
  background: #E34925;
  padding: 20px 0px;
  margin-bottom: 20px;
  width: 1146px;
  position: relative;
  height: 100px;
}
.advantage_list li{
  :before,
  :after{
    position: absolute;
    display: block;
    content: '';
  }
  :before{
    left: -49px;
    top: 0;
    border-style: solid;
    border-width: 50px 50px 50px 0;
    border-color: transparent #E34925 transparent transparent;
  }
  :after{
    right: -49px;
    top: 0;
    border-style: solid;
    border-width: 50px 0 50px 50px;
    border-color: transparent transparent transparent #E34925;
  }
}



.wind_turbines_wrapper{
  display: flex;
  width: 100%;
  align-items: center;

}
.wind_turbines_text_wrapper{
  width: 50%;
}
.wind_turbines_text_inner{
  width: 491px;
  margin-left: auto;
  margin-right: 83px;
}
.wind_turbines_title{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;

}
.wind_turbines_list{
  padding-left: 17px;
}
.wind_turbines_list li{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  list-style: disc;
  margin-top: 20px;
}
.wind_turbines_img{
  width: 50%;
}
.wind_turbines_img img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
.wind_turbines_img img:nth-child(2){
  display: none;
}
.wind_turbines_img img:nth-child(3){
  display: none;
}


.double_bg_img{
  width: 100%;
  height: auto;
}
.double_bg_img img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}


.team_wrapper{
  background: #D6D6D6;
  margin-top: -39px;
  display: flex;
  padding: 80px 0;
}
.team_title_inner{
  width: 50%;
}
.team_title{
  width: 470px;
  margin-left: auto;
  margin-right: 85px;

  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */


  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.team_function_list{
  width: 50%;
  padding-left: 86px;
}
.team_function_list li{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */


  /* Ҹ���� ������ */

  color: #2B2A28;
  list-style: disc;
  width: 95%;
  margin-bottom: 10px;
}

.tab_wrapper{
  display: flex;
  width: 100%;
  align-items: center;
}
.tab_img_wrapper{
  width: 50%;
}
.tabs_content_wrapper{
  width: 50%;
}
.tab_img_inner{
  width: 100%;
}
.tab_img_inner img:nth-child(2){
  display: none;
}
.tab_img_inner img:nth-child(3){
  display: none;
}
.tabs_content_title{
  margin-left: 83px;
  width: 490px;
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */
  letter-spacing: 0.05em;

  /* Ҹ���� ������ */

  color: #2B2A28;
}
.tabs_item_list{
  margin-top: 30px;
}
.tabs_item_item{
  display: flex;
  align-items: center;
  padding-left: 47px;
  border-top: 1px solid #ECECEC;
  &:last-child{
    border-bottom: 1px solid #ECECEC;
  }
}
.tabs_item_item p{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  /* Ҹ���� ������ */

  color: #2B2A28;
  width: 496px;
  padding: 10px 0;
  padding-left: 30px;
}
.tabs_item_arrow{
  width: 35px;
  height: 100%;
}
.tabs_content_sub_title{
  font-family: @helvetica;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  display: flex;
  align-items: center;

  /* Ҹ���� ������ */

  color: #2B2A28;
  width: 525px;
  margin-left: 47px;
  margin-top: 40px;
}

.hide{
  display: none;
}
.tab_img_inner img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.advantage_wrapper{
  display: flex;
  flex-direction: column;
  width: 100%;
  background: #E34925;
  padding-top: 90px;
  padding-bottom: 100px;
}
.advantage_title_wrapper{
  margin: 0 auto;
  width: 1146px;
}
.advantage_title{
  font-family: @mont;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 135%;
  /* or 43px */

  letter-spacing: 0.05em;
  text-transform: uppercase;

  /* ������-����� */

  color: #F5F5F5;
}
.advantage_field_wrapper{
  width: 1146px;
  margin: 0 auto;
}
.advantage_field_sub_wrapper{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.advantage_item{
  width: 31.7%;
  border: 1px solid #F5F5F5;
  margin-top: 33px;
  display: flex;
  align-items: center;
  height: 200px;
}
.advantage_text{
  font-family: @helvetica;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 145%;
  /* or 26px */

  text-align: center;

  /* ������-����� */

  color: #F5F5F5;
  padding-left: 40px;
  padding-right: 40px;
  &span{
    font-weight: 300;
  }
}
.advantage_item:hover{
  background: #F5F5F5;
  /* ������-����� */

  border: 1px solid #F5F5F5;
  box-sizing: border-box;
  /* 1 */

  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);
  cursor: pointer;
  .advantage_text{
    color: #2B2A28;
  }
}
.before_line:before{
  display: none;
}

